import "./App.css";
import React, { useState, useEffect } from "react";
import Admin from "./Admin";

// Firebase App (the core Firebase SDK) is always required and
// must be listed before other Firebase SDKs
import firebase from "firebase/app";

// Add the Firebase services that you want to use
import "firebase/auth";
import "firebase/firestore";
import SignedIn from "./SignedIn";
import { VscLoading } from "react-icons/vsc";
import Players from "./Players";

const firebaseConfig = {
    apiKey: "AIzaSyDIhv-6yxI-Qi9G8MVMaG3DjfE8DC_E2kU",
    authDomain: "vrahdmt.firebaseapp.com",
    projectId: "vrahdmt",
    storageBucket: "vrahdmt.appspot.com",
    messagingSenderId: "310828679483",
    appId: "1:310828679483:web:95234faf9b688d2662b71e",
};

if (!firebase.apps.length) {
    firebase.initializeApp(firebaseConfig);
} else {
    firebase.app(); // if already initialized, use that one
}

// firebase.initializeApp(firebaseConfig);
var db = firebase.firestore();

function App() {
    const [email, setEmail] = useState("");
    const [name, setName] = useState("");
    const [password, setPassword] = useState("");
    const [isSignedIn, setIsSignedIn] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [isAdmin, setIsAdmin] = useState(false);
    const [users, setUsers] = useState([]);
    const [isDone, setIsDone] = useState(false);
    const [user, setUser] = useState({ uid: "", name: "" });
    const [docID, setDocID] = useState("");
    const [screen, setScreen] = useState(true);
    const [playersScreen, setPlayersScreen] = useState(false);

    useEffect(() => {
        fetchUsers();
    }, []);

    const signUp = (email, password) => {
        if (email === "admin" && password === "lubimterku") {
            setIsAdmin(true);
        } else {
            firebase
                .auth()
                .createUserWithEmailAndPassword(email, password)
                .then((userCredential) => {
                    // Signed in
                    var user = { ...userCredential.user, kills: 0 };
                    // console.log("user:", user);
                    addUIDtoDB(user);
                    setIsSignedIn(true);
                })
                .catch((error) => {
                    var errorCode = error.code;
                    var errorMessage = error.message;
                    console.log(errorMessage);
                });
        }
    };

    const signIn = (email, password) => {
        if (email === "admin" && password === "lubimterku") {
            setIsAdmin(true);
        } else {
            firebase
                .auth()
                .signInWithEmailAndPassword(email, password)
                .then((userCredential) => {
                    // Signed in
                    var user = userCredential.user;
                    setUser({ uid: user.uid, name, kills: user.kills });
                    setIsSignedIn(true);

                    // ...
                })
                .catch((error) => {
                    var errorCode = error.code;
                    var errorMessage = error.message;
                });
        }
    };
    const addUIDtoDB = (user) => {
        db.collection("users")
            .add({
                uid: user.uid,
                name,
                kills: user.kills,
            })
            .then((docRef) => {
                console.log("Document written with ID: ", docRef.id);
            })
            .catch((error) => {
                console.error("Error adding document: ", error);
            });
        setUser({ uid: user.uid, name, kills: user.kills });
    };

    const deleteUser = (id) => {
        console.log("somtu");
        console.log(id);
        db.collection("users")
            .doc(id)
            .delete()
            .then(() => {
                console.log("Document successfully deleted!");
            })
            .catch((error) => {
                console.error("Error removing document: ", error);
            });

        const user = firebase.auth().currentUser;
        user.delete()
            .then(() => {
                // User deleted.
            })
            .catch((error) => {
                // An error ocurred
                // ...
            });
        setIsDone(true);
    };

    const deleteUserStep = () => {
        if (window.confirm("Naozaj si zomrel a končíš hru?")) {
            findKiller();
            db.collection("users")
                .where("uid", "==", user.uid)
                .get()
                .then((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        // doc.data() is never undefined for query doc snapshots
                        deleteUser(doc.id);
                    });
                })
                .catch((error) => {
                    console.log("Error getting documents: ", error);
                });
            setIsLoading(true);
        }
    };

    const addKillToKiller = (killerDocID) => {
        const userIndex = users.findIndex((item) => item.uid === user.uid);
        if (userIndex === 0) {
            var killerIndex = users.length - 1;
        } else {
            var killerIndex = userIndex - 1;
        }
        const killer = users[killerIndex];
        console.log("som hrac:", user.name);
        console.log("mna ma zabit:", killer.name);
        var killerRef = db.collection("users").doc(killerDocID);

        const newKills = killer.kills + 1;
        console.log(newKills);

        // Set the "capital" field of the city 'DC'
        killerRef
            .update({
                kills: killer.kills + 1,
            })
            .then(() => {
                console.log("Document successfully updated!");
            })
            .catch((error) => {
                // The document probably doesn't exist.
                console.error("Error updating document: ", error);
            });
    };

    const findKiller = async () => {
        const userIndex = users.findIndex((item) => item.uid === user.uid);
        if (userIndex === 0) {
            var killerIndex = users.length - 1;
        } else {
            var killerIndex = userIndex - 1;
        }
        const killerUID = users[killerIndex].uid;

        await getDocIdByUID(killerUID);
    };

    const getDocIdByUID = async (uid) => {
        await db
            .collection("users")
            .where("uid", "==", uid)
            .get()
            .then((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                    // doc.data() is never undefined for query doc snapshots
                    console.log("id killera:", doc.id);
                    addKillToKiller(doc.id);
                    // return doc.id;
                });
            })
            .catch((error) => {
                console.log("Error getting documents: ", error);
            });
    };

    const deleteUserAdmin = (name, uid) => {
        if (window.confirm(`Naozaj chceš vymazať hráča ${name}?`)) {
            db.collection("users")
                .where("uid", "==", uid)
                .get()
                .then((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        // doc.data() is never undefined for query doc snapshots
                        deleteUserAdminDone(doc.id);
                    });
                })
                .catch((error) => {
                    console.log("Error getting documents: ", error);
                });
            setIsLoading(true);
        }
    };

    const deleteUserAdminDone = (id) => {
        db.collection("users")
            .doc(id)
            .delete()
            .then(() => {
                console.log("Document successfully deleted!");
            })
            .catch((error) => {
                console.error("Error removing document: ", error);
            });
        setIsLoading(false);
        fetchUsers();
    };

    const fetchUsers = () => {
        db.collection("users")
            .get()
            .then((querySnapshot) => {
                var usersDB = [];
                querySnapshot.forEach((doc) => {
                    usersDB.push({
                        uid: doc.data().uid,
                        name: doc.data().name,
                        kills: doc.data().kills,
                    });
                });
                usersDB.sort((a, b) => (a.uid > b.uid ? 1 : -1));

                setUsers(usersDB);
            });
    };
    const handleSignUp = (e) => {
        e.preventDefault();
        setIsLoading(true);
        signUp(email, password);
        setIsLoading(false);
        setPassword("");
        setEmail("");
    };
    const handleSignIn = (e) => {
        e.preventDefault();
        setIsLoading(true);
        signIn(email, password);
        setIsLoading(false);
        setPassword("");
        setEmail("");
    };
    if (playersScreen) {
        return <Players users={users} />;
    }
    if (isDone) {
        return (
            <div className="user">
                <h3 style={{ marginLeft: "10%", marginRight: "10%" }}>
                    Nateraz si v tejto hre skončil. Tvoj účet bol vymazaný.
                </h3>
                <h2 style={{ fontSize: "2rem" }}>Vďaka za hru.</h2>
            </div>
        );
    }
    if (isLoading) {
        return (
            <div className="user">
                <VscLoading className="loading" />
            </div>
        );
    }
    if (isAdmin) {
        return (
            <Admin
                fetchUsers={fetchUsers}
                users={users}
                deleteUserAdmin={deleteUserAdmin}
            />
        );
    }
    if (isSignedIn) {
        return (
            <SignedIn
                fetchUsers={fetchUsers}
                users={users}
                user={user}
                setUser={setUser}
                deleteUser={deleteUserStep}
                findKiller={findKiller}
            />
        );
    }
    return (
        <div className="App">
            {screen ? (
                <div className="section">
                    <div className="container">
                        <div className="card-front">
                            <div className="center-wrap">
                                <div className="section text-center">
                                    <h4 className="mb-4 pb-3">Prihlásenie</h4>
                                    <form onSubmit={handleSignIn}>
                                        <div className="form-group">
                                            <input
                                                type="text"
                                                className="form-style"
                                                placeholder="E-mail"
                                                value={email}
                                                onChange={(e) =>
                                                    setEmail(e.target.value)
                                                }
                                            />
                                        </div>
                                        <div className="form-group">
                                            <input
                                                type="password"
                                                className="form-style"
                                                placeholder="Heslo"
                                                value={password}
                                                onChange={(e) =>
                                                    setPassword(e.target.value)
                                                }
                                            />
                                        </div>
                                        <button className="btn" type="submit">
                                            Prihlás ma
                                        </button>
                                        <h2
                                            className="link"
                                            onClick={() =>
                                                setPlayersScreen(!playersScreen)
                                            }
                                        >
                                            leaderboard
                                        </h2>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            ) : (
                <div className="section">
                    <div className="container">
                        <div className="card-front">
                            <div className="center-wrap">
                                <div className="section text-center">
                                    <h4 className="mb-4 pb-3">Registrácia</h4>
                                    <form onSubmit={handleSignUp}>
                                        <div className="form-group">
                                            <input
                                                type="text"
                                                className="form-style"
                                                placeholder="Meno a priezvisko"
                                                value={name}
                                                onChange={(e) =>
                                                    setName(e.target.value)
                                                }
                                            />
                                        </div>
                                        <div className="form-group">
                                            <input
                                                type="text"
                                                className="form-style"
                                                placeholder="E-mail"
                                                value={email}
                                                onChange={(e) =>
                                                    setEmail(e.target.value)
                                                }
                                            />
                                        </div>
                                        <div className="form-group">
                                            <input
                                                type="password"
                                                className="form-style"
                                                placeholder="Heslo"
                                                value={password}
                                                onChange={(e) =>
                                                    setPassword(e.target.value)
                                                }
                                            />
                                        </div>
                                        <button className="btn" type="submit">
                                            Registruj ma
                                        </button>
                                        <h2
                                            className="link"
                                            onClick={() =>
                                                setPlayersScreen(!playersScreen)
                                            }
                                        >
                                            leaderboard
                                        </h2>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}

export default App;
