import React, { useState, useEffect } from "react";
import { GiDeathSkull } from "react-icons/gi";
import { VscLoading } from "react-icons/vsc";

const SignedIn = ({ users, fetchUsers, user, deleteUser }) => {
    const [target, setTarget] = useState("");
    const [load, setLoad] = useState(true);
    console.log(user);
    useEffect(() => {
        fetchUsers();
        fetchTarget();
    }, []);
    const userDeath = () => {
        deleteUser();
    };
    const fetchTarget = () => {
        const targetIndexSetup = users.filter(
            (item) => item.uid === user.uid
        )[0];
        const targetIndex = users.indexOf(targetIndexSetup);
        console.log(targetIndex);
        if (targetIndex + 1 === users.length) {
            console.log(users[0]);
            setTarget(users[0]);
        } else {
            console.log(users[targetIndex + 1]);
            setTarget(users[targetIndex + 1]);
        }
        setLoad(false);
    };
    const fetchKillerUID = () => {
        const targetIndexSetup = users.filter(
            (item) => item.uid === user.uid
        )[0];
        const targetIndex = users.indexOf(targetIndexSetup);
        console.log(targetIndex);
        if (targetIndex + 1 === users.length) {
            console.log(users[0]);
            setTarget(users[0]);
        } else {
            console.log(users[targetIndex + 1]);
            setTarget(users[targetIndex + 1]);
        }
        setLoad(false);
    };
    const pluralizeKills = (kills) => {
        console.log(kills);
        if (kills === 1) {
            return "cieľ";
        } else if (kills === 0) {
            return "cieľov";
        } else if (kills < 5) {
            return "ciele";
        } else {
            return "cieľov";
        }
    };
    if (load) {
        return (
            <div className="user">
                <VscLoading className="loading" />
            </div>
        );
    }
    return (
        <div className="user">
            {/* <section className="header">
                <span className="line"></span>
                <span className="line2"></span>
                ASSASSIN
            </section> */}
            <h3>Tvoj ďalší cieľ je:</h3>
            <h1>{target.name}</h1>
            {`Eliminoval si ${user.kills} ${pluralizeKills(user.kills)}.`}
            <span></span>
            <GiDeathSkull className="death" onClick={userDeath} />
        </div>
    );
};

export default SignedIn;
