import React from "react";
import { GiDeathSkull } from "react-icons/gi";

const Players = ({ users }) => {
    console.log(users);
    const shuffledUsers = [...users];
    shuffle(shuffledUsers);
    console.log(shuffledUsers);
    function shuffle(array) {
        var currentIndex = array.length,
            randomIndex;

        // While there remain elements to shuffle...
        while (currentIndex != 0) {
            // Pick a remaining element...
            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex--;

            // And swap it with the current element.
            [array[currentIndex], array[randomIndex]] = [
                array[randomIndex],
                array[currentIndex],
            ];
        }

        return array;
    }
    return (
        <div className="contLeaderboard">
            <article className="leaderboard">
                <header>
                    <h1 className="leaderboard__title">
                        <span className="leaderboard__title--top">
                            Assassin
                        </span>
                        <span className="leaderboard__title--bottom">
                            Leaderboard
                        </span>
                    </h1>
                </header>

                <main className="leaderboard__profiles">
                    {shuffledUsers.map(({ uid, name, kills }) => (
                        <article key={uid} className="leaderboard__profile">
                            <span className="leaderboard__name">{name}</span>
                            <span className="leaderboard__value">
                                <GiDeathSkull className="death2" />
                                {kills}
                                <span></span>
                            </span>
                        </article>
                    ))}
                </main>
            </article>
        </div>
    );
};

export default Players;
